//Text Analytics



.page-treereport {
  .tabs--tree {
    li {
      a {
        position: relative;
        padding: 23px 30px 23px 20px;
        padding-left: 60px;
        .icon {
          position: absolute;
          left: 20px;
          top: 19px;
          color: $white;
          &:before {
            color: $white;
            line-height: 30px;
            font-size: emify(10, 10);
          }

          &.icon-all {
           // @include sprite($all);
            left: 30px;
            top: 24px;
          }
          &.icon--circle {
            @include border-radius(50%);
            width: 30px;
            text-align: center;
            height: 30px;
            background: $white;
          }
        }

      }

      &.positive {
        a {
          .icon {
            background: $clr-green;

          }
        }

      }

      &.neutral {
        a {
          .icon {
            background: $clr-amber;
            font-style: normal;
            font-size: emify(15, 10);

          }
        }

      }

      &.negative {
        a {
          .icon {
            background: $clr-red;

          }
        }

      }

      &.unassigned {
        a {
          .icon {
            background: $clr-gray;

          }
        }

      }
    }
  }

}

.tree-report {
  @include clearfix();
  clear: both;
  @include span(9 no-gutters);

  @include susy-breakpoint(1024px, $susy, '.no-mq') {
    @include span(12 no-gutters);
  }

  .report__col {
   // @include span(4 no-gutters);
    width:38%;
    float: left;

    &:first-child{
      width: 24%;
    }
    @include susy-breakpoint(1024px, $susy, '.no-mq') {
      width:100%;
    }

    //border-bottom: 1px solid #e7e7e7;

    .col__inner {
      @include clearfix();
      min-height: 550px;
      border-left: 1px solid #e7e7e7;
    //  overflow-y: auto;
    //  padding-bottom: 10px;

      ul{
        height: 550px;
        padding-bottom: 10px;
        overflow-y: auto;

        .mCSB_scrollTools{
          right: 0;
        }

        .mCSB_inside>.mCSB_container {
         // margin-right: 15px;
        }
      }

      @include susy-breakpoint(1024px, $susy, '.no-mq') {
        height: 300px;
      }
    }

    &:first-child {

      .col__inner {
        border: 0;
        background:#f7f7f7;
      }

    }

    .col__title {
      text-transform: uppercase;
      color: #5b6c7c;
      font-size: emify(15, 10);
      padding: emify(10, 10) 0;
      padding-left: emify(15, 10);
      border-bottom: 1px solid #e7e7e7;

    }
  }
}

.tree-comments {
  @include span(3 no-gutters);

  @include susy-breakpoint(1024px, $susy, '.no-mq') {
    @include span(12 no-gutters);
  }

  .select-filter {
    background: none;
  }

  .select__field {

    width: 100%;

  }

  .comments__inner {
    @include clearfix();
    min-height:550px;
    position: relative;
    @include susy-breakpoint(1024px, $susy, '.no-mq') {
      height: auto;
    }
    border-left: 1px solid #e7e7e7;
  }

  .comments__title {
    text-transform: uppercase;
    color: #5b6c7c;
    font-size: emify(15, 10);
    padding: emify(10, 10) 0;
    padding-left: emify(15, 10);
    border-bottom: 1px solid #e7e7e7;

  }
}

.report-list {
  .list__item {
    a {
      display: block;
      padding:10px 15px;;
      font-size: emify(14, 10);
      color: #4c5e70;

      em {
        font-style: normal;
        color: $clr-pri;
      }

      &:hover {
        color: $clr-pri;
      }
    }

    &.active {
      a {
        background: #f7f7f7;
        border-left: 5px solid $clr-pri;
        position: relative;

        &:before {
          font-family: 'Icomoon';
          content: "\f0da";
          position: absolute;
          right: 15px;
        }
      }
    }

  }

  &.list--area {
    .list__item {
      a {
        padding: emify(15, 10) emify(17, 10);
        border-bottom: 1px solid #e7e7e7;
        text-transform: uppercase;
        em {
          color: #4c5e70;
        }

      }

      &.active {
        a {
          background: $white;
          // border-left:3px solid $clr-pri;

          em {
            color: $clr-pri;
          }
        }
      }
    }

  }

}

.comments-list {
  padding: 0 emify(15, 10);
  // @include clearfix();
  position: relative;
  clear: both;
  .list__item {

    box-shadow: 0px 0px 10px 0px rgba(0, 0, 0, 0.05);
    display: block;
    margin-bottom: 15px;
    padding: emify(10, 10);
    position: relative;
    padding-left: 35px;

    &:before {
      position: absolute;
      left: 10px;
      font-family: 'Icomoon';
      content: '=';
      font-size: 18px;
    }

    &.neutral {
      &:before {
        color: $clr-amber;
        content: "\e902";

      }
    }

    &.positive {
      &:before {
        content: "\f087";
        color: $clr-green;
      }
    }

    &.negative {
      &:before {
        content: "\f088";
        color: $clr-red;
      }
    }

    &.unassigned {
      &:before {
        content: "\f05e";
        color: $clr-gray;
      }
    }

    p {
      font-size: emify(12, 10);

    }
  }

}

.map-legends {
  width: 240px;
  height: 27px;
  position: absolute !important;
  background: $white;
  -webkit-box-shadow: 0 1px 1px rgba(0, 0, 0, 0.15);
  -moz-box-shadow: 0 1px 1px rgba(0, 0, 0, 0.15);
  box-shadow: 0 1px 1px rgba(0, 0, 0, 0.15);
  top: 10px !important;
  right: 10px !important;
  @include border-radius(3px);
  overflow: hidden;

  li {
    display: inline-block;
    padding: 8px;
    padding-left: 30px;
    position: relative;
    cursor: pointer;

    &.active {
      background: #f8f9fa;
    }

    .icon {
      position: absolute;
      left: 10px;
      top: 5px;

      &:before {

        font-size: 16px;
      }
    }
  }

}

.table {
  &.cloud--table {
    th {
      .icon {
        &:before {
          font-size: 16px;
        }
      }
    }

  }
}

.dealerTitle_Tooltip {
  font-size: 14px;
}

#map-canvas {
  margin: 0;
  padding: 0;
  height: 400px;
  max-width: none;
}

#map-canvas img {
  max-width: none !important;
}

