//popup

$footerbg: #f8f9fa;
.modal {
  position: fixed;
  top: 0;
  bottom: 0;
  right: 0;
  left: 0;
  z-index: map-get($zindex, modal);
  display: table;
  height: 100%;
  width: 100%;
  @include opacity(0);
  visibility: hidden;
  @include transition();

  @include susy-breakpoint(1024px, $susy, '.no-mq') {
    display: block;
   // background: $footerbg;
    padding: 10px;
  }

  .modal__background {
    position: absolute;
    top: 0;
    bottom: 0;
    right: 0;
    left: 0;
    z-index: 1;
    background: rgba(0, 0, 0, 0.3);
    @include susy-breakpoint(1024px, $susy, '.no-mq') {
     // display: none;
    }
  }
  .modal__outer {
    display: table-cell;
    vertical-align: middle;
    text-align: center;
    @include susy-breakpoint(1024px, $susy, '.no-mq') {
      display: block;
    }

    .modal__inner {
      position: relative;
      z-index: map-get($zindex, modalcontent);
      overflow: hidden;
      text-align: left;
      display: inline-block;
      background: $white;
      transform: translateY(45px);
      @include transition();
      @include border-radius(3px);
      visibility: hidden;
      @include opacity(0);
      max-width: 1200px;
      width: 100%;

      @include susy-breakpoint(1024px, $susy, '.no-mq') {
        display: block;
       // border-radius: 0;
        max-width: 100%;
      }

      .modal__header {
        padding: 15px 20px;
        border-bottom: 1px solid #d7d7d7;
        position: relative;
        width: 100%;
        float: left;

        @include susy-breakpoint(1024px, $susy, '.no-mq') {
          z-index: 1;
          width: 100%;
          top: 0;
          background: $white;
          position: fixed;
        }

        &.margin--none {
          margin-bottom: 0;
        }
      }
      .modal__title {
        font-size: 14px;
        font-weight: 500;
        position: relative;
        float: left;
      }

    }

    .modal__content {
      max-height: 550px;
      overflow-y: auto;
      min-height: 100px;
      position: relative;
      float: left;
      width: 100%;
      background: $white;
      &.mCustomScrollbar {
        @include susy-breakpoint(1024px, $susy, '.no-mq') {
          -ms-touch-action: auto;
          touch-action: auto;
        }
      }

      @include susy-breakpoint(1369px) {
        max-height: 400px;
      }

      @include susy-breakpoint(1024px, $susy, '.no-mq') {

        max-height: none;

      }
    }
    .modal__footer {
      @include clearfix();
      text-align: right;
      clear: both;
      background: #f4f5f9;
      padding: 10px 15px;
      border-top: 1px solid #d7d7d7;
      @include border-bottom-radius(3px);

      @include susy-breakpoint(1024px, $susy, '.no-mq') {
        position: absolute;
        width: 100%;
        bottom: 0;
        z-index: 1;

      }

      button{
      //  display: inline;
    //    float: right;
        vertical-align: top;
      }
      .btn {
        margin: 0 2px;
        font-size: 12px;
        padding: 7px 20px;
      }
    }
  }

  &.active {
    @include opacity(1);
    visibility: visible;
    .modal__outer {
      .modal__inner {
        transform: translateY(0);
        @include opacity(1);
        visibility: visible;
      }
    }
  }

}

.modal {
  .loader-overlay {
    position: absolute;
  }
}

.modal {
  &.modal--xs {
    .modal__outer {
      .modal__inner {
        width: 450px;
      }
    }

  }
}

.modal {
  &.modal--small {
    .modal__outer {
      .modal__inner {
        width: 550px;
      }
    }

  }
}

.modal {
  &.modal--medium {
    .modal__outer {
      .modal__inner {
        width: 750px;
      }
    }

  }
}



