.page-commentlist {
  .table {

    table {
      border-spacing: 0 3px;
      border-collapse: separate;

      td, th {
        &:last-child {
          width: 38%;
        }
      }
    }

    tbody {
      tr {
        background: #f8f9fa;
        &.thead {
          background: none;
        }
        &.active {
          border-bottom: 0;
          background: #f3f3f3
        }

        &.expanded {
          td {
            background: #fafafa;
            padding: 0;
            font-weight: normal;
          }
        }

        &.bar {
          td:nth-child(2) {
            //border-left: 4px solid #34a2e5;
            position: relative;

            &:before {
              content: '';
              width: 4px;
              height: 100%;
              background: $clr-pri;
              position: absolute;
              left: 0;
              top: 0;

            }
          }
        }
      }

    }

  }

  #GTPanelItem, #GTPanelItemContent {
    overflow: visible !important;
  }

}

//$bg-clr:#f9f9f9;
$border-clr: #e1e1e1;
$notification: #5b6c7c;
.expanded__content {
  @include clearfix();
  float: left;
  width: 100%;
  padding: 20px 0;
  .content__left {
    border-right: 1px solid #e1e1e1;
    @include span(7 no-gutters);
    //  @include span(12 no-gutters);
    padding: 0 20px;
    // min-height:200px;

    .breadcrumbs {

      padding: 0;
      margin-bottom: 10px;

      .icon {
        padding-left: 20px;
        position: relative;

        &:after {
          position: absolute;
          left: 0;
          font-family: 'Icomoon';
          content: '=';
          font-size: 16px;
        }

        &.neutral {
          &:after {
            color: $clr-amber;
            content: "\e902";

          }
        }

        &.positive {
          &:after {
            content: "\f087";
            color: $clr-green;
          }
        }

        &.negative {
          &:after {
            content: "\f088";
            color: $clr-red;
          }
        }

        &.unassigned {
          &:after {
            content: "\f05e";
            color: $clr-gray;
          }
        }
      }
    }

  }
  .content__right {
    @include span(5 no-gutters);
    padding: 0 20px;
  }
}

hr {
  border: 0;
  clear: both;
  float: left;
  width: 100%;
  margin: emify(5, 10) emify(0, 10);
  border-top: 1px solid $border-clr;
}

.testimonial {
  float: left;
  padding: emify(10, 10) 0;
  padding-top: emify(28, 10);
  @include clearfix();
  // width: 50%;
  position: relative;
  &:before {
    position: absolute;
    left: 0;
    color: #efefef;
    top: 0;
    font-size: emify(50, 10);
    content: "\f10d";
    font-family: 'icomoon';
  }
  p {
    font-style: italic;
    font-size: emify(11, 10);
    position: relative;
  }
}

.translation {
  float: left;
  padding: emify(10, 10) 0;
  @include clearfix();
  width: 100%;
  position: relative;
  h2 {
    margin-bottom: emify(10, 10);
  }
  p {
    font-size: emify(11, 10);
    position: relative;
    margin-bottom: emify(10, 10);
    &.translation__notification {
      color: $notification;
    }
  }
  .translation__content {
    float: left;
    width: 40%;
    padding: emify(5, 10);
  }
  .translation__form {
    float: right;
    width: 50%;
    .form__label {
      color: $notification;
      display: block;
      margin-bottom: emify(10, 10);
    }
    textarea {
      width: 100%;
      height: 70px;
      @include border-radius(6px);
      padding: emify(5, 10);
    }
  }
}

$cat-border: #e7e7e7;
$media-clr: #d5d5d5;
//
.category {
  float: left;
  padding: 0 0 20px;
  @include clearfix();
  width: 100%;
  position: relative;
  //border-bottom: 1px solid $cat-border;
  .incompleteClassification {
    float: left;
    width: 100%;
    padding: emify(5, 10) emify(15, 10);
  }
  .category__content {
    @include span(4);
    .category__label {
      color: $notification;
      display: block;
      margin-bottom: emify(7, 10);
      font-weight: 700;
    }
    .category__field {
      position: relative;
    }
    .category__link {
      display: block;
      border: 1px solid $clr-pri;
      background: $clr-pri;
      @include border-radius(20px);
      padding: emify(5, 10) emify(10, 10);
      font-size: emify(13, 10);
      // background: #f9f9f9;
      color: $white;
      padding-right: emify(25, 10);
      .icon {
        position: absolute;
        top: 6px;
        right: 15px;
        &:before {
          vertical-align: top;
        }
      }
      &:hover,
      &:active {
        color: $white;
        background-color: darken($clr-pri, 15%);
      }
      &.active {
        z-index: 2;
        position: relative;
        //background-color: darken($clr-pri, 8%);
        //color: $white;
      }
    }
  }
  .sentiment__content {
    @include span(4);
    .sentiment__label {
      color: $notification;
      display: block;
      margin-bottom: emify(10, 10);
      font-weight: 700;
    }
    .sentiment__media {
      li {
        display: inline-block;
        margin-right: emify(5, 10);
        color: $media-clr;
        i {
          cursor: pointer;
          &:before {
            vertical-align: middle;
            font-size: 24px;
          }
          &.positive {
            color: $media-clr;
            &.active {
              color: $clr-green;
            }
          }
          &.neutral {
            color: $media-clr;
            &.active {
              color: $clr-amber;
            }
          }
          &.negative {
            color: $media-clr;
            &.active {
              color: $clr-red;
            }
          }
        }
      }
    }
  }
  .primary__content {
    @include span(4);
    .primary__label {
      color: $notification;
      display: block;
      margin-bottom: emify(10, 10);
      font-weight: 700;
    }
    .primary__media {
      li {
        display: inline-block;
        margin-right: emify(5, 10);
        color: $media-clr;
        i {
          font-size: 24px;
          &:before {
            vertical-align: middle;
          }
        }
        &.active {
          color: $clr-pri;
        }
      }
    }
  }
  .remove-category {
    float: right;
    width: 6%;
    a {
      color: $clr-red;

      &:before {
        font-size: 24px;
      }

      &:hover {
        //color: $media-clr;
      }
      &.disabled {
        color: $media-clr;
        opacity: 0.9;
        cursor: default;
      }
    }
  }

}

.add-category {
  float: right;
  width: 100%;
  text-align: right;
  border-top: 1px solid #e1e1e1;
  padding: 10px;

  a {
    font-weight: 700;
    font-size: 12px;
    text-transform: uppercase;
  }
  .icon {

    &:before {
      vertical-align: middle;
    }
  }
}

.expanded__control {
  float: left;
  @include clearfix();
  width: 100%;
  text-align: right;
  background: $white;
  padding: emify(15, 10) emify(10, 10);
  border-top: 1px solid #e4e9ed;
  .btn {
    margin-left: emify(5, 10);
    .icon {
      &:before {
        vertical-align: initial;
      }

    }
  }
}

.category__overlay {
  left: 0;
  background: $white;
  overflow: hidden;
  position: absolute;
  border: 3px solid $clr-pri;
  @include border-radius(10px);
  width: 235px;
  z-index: 1;
  text-align: left;
  padding: 0;
  box-shadow: 0 0px 2px rgba(196, 196, 196, 0.9);
  @include transition();

  .overlay__header {
    background: #f1f6f9;
    border-bottom: 1px solid #e5e5e5;
    padding: 10px 20px;

    li{
      a{
        color: #4c6377;
        font-size: 13px;
      }

    }

  }

  .overlay__content {
    padding:15px 20px;
    ul {

      height:165px;
      overflow-y: auto;

      li {
        display: block;
        a {
          display: block;
          padding:10px 5px;
          font-size: 13px;
          border-bottom: 1px solid #f3f3f3;
          position: relative;
          color: $clr-blue;
          cursor: pointer;

          &:before {
            content: "\f105";
            font-family: icomoon;
            position: absolute;
            right:8px;
            color:$clr-pri;
            font-size:16px;
            top:10px;
          }

          &:hover {
            //color: $white;
            background: #f1f6f9;
            &:before {
            //  color: $white;
            }
          }

        }

        &.active {

          a {
          //  color: $white;
            background: #f1f6f9;
            // background: linear-gradient(#59b9ef, #006fd5);
            &:before {
            //  color: $white;
            }
          }

        }

        &.no--child {
          a {
            &:before {
              display: none;
            }
          }
        }
      }
    }
  }

  opacity: 0;
  transform: translateY(0);
  visibility: hidden;
  &.active {
    opacity: 1;
    transform: translateY(12px);
    visibility: visible;
  }
  .overlay__footer {
    text-align: center;
    padding: 10px 20px;

    .btn {
      display: block;
    }

  }
}


.comment-portal {
  float: right;
  width:150px;
  max-width: 20%;
}