@import 'core';
@import 'animate';
@import 'autoheight';
@import 'lazyload';
@import 'video';

.carousel-wrap {
  margin: 30px auto 0;
  padding:10px 40px;
  width: 100%;
  border-top: 1px solid #ebebeb;

  position: relative;

  &:before {
    top: 0;
    width: 30px;
    height: 100%;
    background-color: rgba(255, 255, 255, 0);
    background-image: -webkit-linear-gradient(left, #fff, rgba(255, 255, 255, 0));
    background-image: -moz-linear-gradient(left, #fff, rgba(255, 255, 255, 0));
    background-image: -o-linear-gradient(left, #fff, rgba(255, 255, 255, 0));
    background-image: -ms-linear-gradient(left, #fff, rgba(255, 255, 255, 0));
    background-image: linear-gradient(to right, #fff, rgba(255, 255, 255, 0));
    content: '';
    z-index: 2;
    position: absolute;
    display: none;
  }

  &:after {
    top: 0;
    right: 20px;
    width: 30px;
    height: 100%;
    background-color: rgba(255, 255, 255, 0);
    background-image: -webkit-linear-gradient(right, #fff, rgba(255, 255, 255, 0));
    background-image: -moz-linear-gradient(right, #fff, rgba(255, 255, 255, 0));
    background-image: -o-linear-gradient(right, #fff, rgba(255, 255, 255, 0));
    background-image: -ms-linear-gradient(right, #fff, rgba(255, 255, 255, 0));
    background-image: linear-gradient(to left, #fff, rgba(255, 255, 255, 0));
    content: '';
    z-index: 2;
    position: absolute;
    display: none;
  }
}

/* fix blank or flashing items on carousel */
.owl-carousel {
  // border-top: 1px solid #d6dade;
  //border-bottom: 1px solid #d6dade;
  .item {
    position: relative;
    z-index: 100;
    -webkit-backface-visibility: hidden;
    padding: 5px 20px;
    font-size: 13px;
    text-align: center;
    display: table-cell;
    color: #212229;
    cursor: pointer;

    span{
      color: $clr-pri;
    }

    &.active {
      background: $clr-pri;
      color: $white;
      border-radius: 3px;
      span{
        color: $white;
      }
    }


  }

  &.carousel--child {
    text-align: center;
    .item {
      float: none;
      border-top: 0;
      border-bottom: 0;
    }
  }

}

