/* end fix */
.owl-nav > div {
  // margin-top: -26px;
  position: absolute;
  top: 0;
  color: #34495e;
}

.owl-nav i {
  font-size: 26px;
}

.owl-nav .owl-prev {
  left: -25px;

  @include susy-breakpoint(768px, $susy, '.no-mq') {
    left: -20px;
  }

  &.disabled {
    opacity: 0.3;
  }

}

.owl-nav .owl-next {
  right: -25px;

  @include susy-breakpoint(768px, $susy, '.no-mq') {
    right: -20px;
  }
  &.disabled {
    opacity: 0.3;
  }
}

.fade {
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: linear-gradient(to right, rgba(255, 255, 255, 0) 30%, rgba(255, 255, 255, 1) 100%);
  position: absolute;
}

