.widget--socialmediafeed {

  .widget__content {
    padding: 20px;
  }
  .mCSB_scrollTools {
    right: -5px;
    top: 0;
  }

}

.social-media-feed {
 // height: 250px;
//  overflow-x: auto;
  margin: 10px 0 15px;
}

.media-list {
  > li {
    display: block;
    border-bottom: 1px solid #f1f1f1;
    padding: 15px 0;
    float: left;
    width: 100%;

    .media__img {
      float: left;
      width: 10%;
      text-align: center;

      img {
        @include border-radius(50%);
      }
      .media__initial {
        display: inline-block;
        width: 48px;
        height: 48px;
        border-radius: 50%;
        background: $clr-pri;
        color: $white;
        text-align: center;
        font-size: 24px;
        font-weight: 600;
        line-height: 48px;
      }
      .media__title {
        display: block;
        margin-top: 5px;
        font-size: emify(14, 10);
      }

    }

    .media__description {
      float: right;
      width: 85%;

      p {
        font-size: emify(14, 10);
        margin-bottom: 15px;
        line-height: 18px;
        color: $black;
      }
    }

    .feedback__list {

      li {
        display: inline-block;

        width: auto;
        float: none;
        margin-right: 8px;

        a {
          font-size: 13px;
          color: #b2b2b2;
        }

        &.active {
          a {
            color: #3cb878;
          }

        }
      }
    }
  }
}

$tabactive: #efefef;
.tabs {
  &.tabs--social {
    background: $white;
    position: relative;
    @include clearfix();
    display: block;
    font-size: 1.8rem;
    padding: 0;
    border-bottom: 1px solid #f1f1f1;
    height: auto;
    .tabs__tab {
      padding: emify(12, 12) emify(12, 12);
      margin: 0;
      font-size: 14px;
      text-transform: capitalize;
      color: #cccccc;

      .facebook {
        color: #204289;
      }

      .twitter {
        color: #34ccff;
      }

      .instagram {
        color: #e4405f;
      }

      .dealer {
        color: #e75424;
      }

      .rater {
        color: #24347b;
      }

      .google {
        color: #1e69fb;
      }

      .yelp {
        color: #d32323;
      }

      i {
        font-size: emify(16, 12);
        padding-right: emify(3, 12);
        vertical-align: middle;
      }
      a {
        font-weight: normal;
      }
      &.active {
        background: $tabactive;
        a {
          color: $clr-pri;
        }
        border: 0;
      }
    }
  }
}