//recent comments
$comments-border: #cccccc;
$badge-bg: #89bf5d;
$label-bg: #f35d58;
$date-clr: #556a78;

.widget--recentcomments {
  .widget__content {
    height: 330px;
    padding: 0;
    overflow-y: auto;
    width: 100%;

    .mCustomScrollbar{
      margin-bottom: 15px;
    }
  }
}

.recent-comments {
  font-size: 1rem;
  border-bottom: 1px solid $comments-border;
  padding: emify(17, 10) emify(20, 10);
  @include clearfix();
  &:last-child {
    border: 0;
  }

  .comments__description {
    float: left;
    width: 100%;
    position: relative;
    padding-left: 65px;
    .comments__initial {
      position: absolute;
      background: $clr-pri;
      @include border-radius(100%);
      width: emify(50, 10);
      height: emify(50, 10);
      text-align: center;
      left: 0;

      span {
        font-size: emify(18, 10);
        color: $white;
        @include center();
      }
    }
    .comments__title {
      //  font-family: $font-opensansbold;
      font-weight: 600;
      font-size: emify(14, 10);
      display: inline-block;
      width: 150px;
    }
    .comments__badge {
      float: right;
      background: $badge-bg;
      color: $white;
      padding: emify(2, 10) emify(10, 10);
      @include border-radius(10px);
    }
    p {
      font-size: emify(13, 10);
    }
  }
  .comments__footer {
    padding: emify(10, 10) 0;
    @include clearfix();
    float: left;
    width: 100%;
    .comments__label {
      background: $label-bg;
      color: $white;
      padding: emify(2, 10) emify(10, 10);
      @include border-radius(8px);
      margin-right: emify(5, 10);
      font-size: emify(12, 10);
    }
    .comments__count {
      font-size: emify(12, 10);
    }
    .comments__date {
      float: right;
      font-size: emify(14, 10);
      color: $date-clr;
    }
  }
}
