.main-tabs {
  position: relative;
}

.tabs {
  font-size:16px;
  border-bottom: 3px solid #e1e4e8;

  li {
    display: inline-block;
    padding: 0 40px 0 0;

    a {
      display: block;
      padding: 2px 0 10px;
      color: #212229;
      cursor: pointer;
      position: relative;
      &:after {
        display: none;
        content: "";
        position: absolute;
        left: 0;
        bottom: -3px;
        width: 100%;
        height: 3px;
        background: $clr-pri;
      }

      &:hover,
      &.active {
        color: $clr-pri;
      }
      &.active {
        &:after {
          display: block;
        }
      }
    }
  }

  &.tabs--timeline {
    li {
      a {
        padding: 10px;
      }
    }
  }
}

.tabs-secondary {
  font-size:14px;
  @include susy-breakpoint(1024px, $susy, '.no-mq') {
    font-size:13px;
  }

  li {
    display: inline-block;
    margin-right:0;
    a {
      display: block;
      padding: 8px 15px;
      color: #212229;
      cursor: pointer;
      text-align: center;
      border-radius: 3px;
      position: relative;
      @include susy-breakpoint(1024px, $susy, '.no-mq') {
        padding: 5px 8px;
      }
      span {
      //  color: #a9b4bd;
      }

      &:hover,
      &.active {
        color: $clr-pri;

      }
      &.active {
        background: $clr-pri;
        color: $white;

        span {
         // color: $white;
        }
      }
    }
  }

}