//page-reportgenerator

.rg__filters {
  display: flex;
  border-bottom: 1px solid #ebebeb;
  align-items: baseline;

  .tabs-secondary{
   display: inline-block;
  }
  .filter-item {
    padding: 15px 20px;
    flex: 1 1 0;
    border-left: 1px solid #ebebeb;

    &:first-child {
      border-left: 0;
    }

    .item__label {
      font-size: 12px;
      color: #969dab;
      margin-right:30px;
    }

  }
}

.filter-by {
  display: inline-block;
  .list__item {
    display: inline-block;
    margin-right:30px;

    a {
      position: relative;
      padding: 0 20px 0 0;
      display: inline-block;
      font-size: 12px;
      color: #212229;
      &:after {
        content: "\f107";
        font-size:12px;
        font-family: "Font Awesome 5 Pro";
        font-style: 600;
        color: #b8b8b8;
        position: absolute;
        right:3px;
        top: 50%;
        margin-top: -7px;
      }
    }
  }
}

.report-generator {
  @include container();

}

.rg-drag-column {
  @include span(3 no-gutters);
  border-right: 1px solid #ebebeb;
  min-height:400px;



  .drag__list {
    .list__item {
      a {
        display: block;
        font-size: 12px;
        padding: 12px 15px;
        text-transform: capitalize;
        color: #34495e;
        border-bottom: 1px solid #ebebeb;
        text-decoration: none;
        position: relative;
        background: #f5f6f7;
        &:hover {
          cursor: move;
        }

        &:before {
          font-family: "Font Awesome 5 Pro";
          position: absolute;
          content: "\f105";
          color: #bdc2c6;
          font-size: 14px;
          right: 20px;
          cursor: pointer;
        }
      }

      &.active {
        > a {
          background: #edeef1;
          // color: $white;

          &:before {
            content: "\f107";
          }
        }
      }
      ul {
        li {
          a {
            background: $white;
            padding: 9px 15px;

            &:before {
              &:hover {
                cursor: pointer;
              }
            }
            &:hover {
              background: $clr-pri;
              color: $white;
            }
            &.ui-state-disabled {
              &:hover {
                color: $black;
                opacity: 1;
              }
            }
            &.active {
              span {
                display: none;
              }
            }
          }

          &:first-child {
            a {
              border-radius: 0;
            }
          }
          &.selected {
            a {
              &:before {
                content: "\f00c";
                color: $clr-pri;
                font-size: 12px;
                right: 17px;
              }
              &:hover {
                background: none;
                cursor: default;
                color: inherit;
                &:before {
                }
              }
            }
          }
        }
      }
    }
  }
}

.rg-drop-column {
  @include span(9);
  position: relative;
  min-height:400px;
  padding:20px;

  .filter-save{
    margin-bottom:15px;
  }

  .btn--danger {
    position: absolute;
    bottom: 20px;
    left: 50%;
    margin-left: -41px;
    text-align: center;
  }
}

.drag-content {
  border: 2px dashed #ebebeb;
  padding: 20px;
  text-align: center;
  border-radius: 3px;
  clear: both;
  float: left;
  width: 100%;
  max-height: 500px;
  overflow-y: auto;

  .drag__msg {
    padding: 30px;
    i {
      display: inline-block;
      font-size: 25px;
      color: #d5d7dd;
    }
  }

  p {
    font-size: 12px;
    margin-top: 10px;
  }
}

.drop-list {
  .list__item {
    float: left;
    margin: 0 10px 0 0;
    position: relative;
    font-size: 12px;
    @include transition();
    a {
      color: #34495e;
      border: 1px solid #ebebeb;
      padding: 15px;
      border-radius: 3px;
      display: block;
    }

    i {
      position: absolute;
      top: -7px;
      right: -7px;
      font-size: 10px;
      cursor: pointer;
      opacity: 0;
      z-index: 1;
      background: #ef207d;
      @include transition();
      text-align: center;
      width: 18px;
      color: $white;
      height: 18px;
      line-height: 19px;
      border-radius: 100%;
    }
    &:hover {
      a {
        border-color: #b7b7b7;
      }
      i {
        opacity: 1;
        transform: scale(1.1);
      }
    }
  }
}


.rg__footer {
  border-top: 1px solid #ebebeb;
  padding: 15px;
  margin: 0 0 0;
  position: relative;
  background: #f9fafc;
  text-align: right;
  border-radius: 0 0 3px 3px;

  .btn {
    margin: 0 5px 0 0;
  }
}