.widget--socialmediacards {
  @include clearfix();

  @include nested(15) {
    .socialcard {
      @include span(3 split);
    }
  }

  .socialcard {
    padding: 20px;
    background: #34508d;
    color: $white;
    overflow: hidden;
    cursor: pointer;
    @include border-radius(7px);
    .social-icon {
      font-size: 16px;
      height: 25px;
      position: relative;
      .icon {
        position: absolute;
        font-size: 21px;
        @include transition();
      }
    }
    .comments {
      padding: 30px 0;
      @include transition();
      h3 {
        font-size: 48px;
        line-height: 100%;
        letter-spacing: -3px;
      }
      h4 {
        font-size: 12px;
        text-transform: uppercase;
      }
    }
    .trend {
      text-align: right;
      font-size: 18px;
      .icon {
        font-size: 14px;
        opacity: 0.3;
        @include transition();
      }
    }
    &.facebook--card {
      background-color: #2b4170; /* fallback color */
      background: -moz-linear-gradient(top, #3b5998, #2b4170);
      background: -ms-linear-gradient(top, #3b5998, #2b4170);
      background: -webkit-linear-gradient(top, #3b5998, #2b4170);
    }
    &.twitter--card {
      background-color: #0081ce; /* fallback color */
      background: -moz-linear-gradient(top, #00aced, #0081ce);
      background: -ms-linear-gradient(top, #00aced, #0081ce);
      background: -webkit-linear-gradient(top, #00aced, #0081ce);
    }
    &.instagram--card {
      background: #d6249f;
      background: radial-gradient(circle at 30% 107%, #fdf497 0%, #fdf497 5%, #fd5949 45%,#d6249f 60%,#285AEB 90%);
    }
    &.pinterest--card {
      background-color: #c73f2d; /* fallback color */
      background: -moz-linear-gradient(top, #f73f2d, #c73f2d);
      background: -ms-linear-gradient(top, #f73f2d, #c73f2d);
      background: -webkit-linear-gradient(top, #f73f2d, #c73f2d);
    }
    &.yelp--card {
      background-color: #b82314; /* fallback color */
      background: -moz-linear-gradient(top, #b82314, #981d11);
      background: -ms-linear-gradient(top, #b82314, #981d11);
      background: -webkit-linear-gradient(top, #b82314, #981d11);
    }
    &:hover {
      .trend {
        .icon {
          opacity: 0.8;
        }
      }
      .social-icon {
        .icon {
          transform: scale(2);
          //transform-origin: top left;
          opacity: 0.5;
        }
      }
      .comments {
        padding-left: 10px;
      }
    }
  }

}
