.socialmedia-cards {

  /*margin-left: 0 - gutter();
  margin-right: 0 - gutter();
*/
  .cards__widget {
    //@include span(3);
    @include span(4);
  }
  .card {
    float: left;
    width: 100%;
    border-radius: 6px;
    background: $white;
    padding: 20px;
    box-shadow: 0px 0px 6.6px 4.4px rgba(247, 247, 247, 0.79);
    @include clearfix();
    color: $white;
    position: relative;
    cursor: pointer;

    .card__icon {
      float: left;
      width: 64px;
      height: 64px;
      line-height: 82px;
      font-size: 16px;
      border: 1px solid $white;
      border-radius: 50%;
      text-align: center;
      position: relative;
      .icon {
        font-size: 36px;
        @include transition();
      }
    }
    .card__detail {
      float: left;
      padding: 0;
      margin-left: 20px;
      @include transition();
      h3 {
        font-size: 24px;
        margin: 5px 0 10px;
        line-height: 100%;
      }
      h4 {
        font-size: 14px;
      }
    }
    &.active {
      @include arrow('bottom','center', 10px, $clr-pri);
    }

    &.card--negative {
      background: #eb0045;
      //background: linear-gradient(45deg, rgba(235, 0, 69, 1) 0%, rgba(253, 88, 95, 1) 100%);
      &.active {
        @include arrow('bottom','center', 10px, #eb0045);
      }
    }

    &.card--positive {
      background: #66d069;
      //background: linear-gradient(45deg, rgba(102, 208, 105, 1) 0%, rgba(37, 213, 198, 1) 100%);
      &.active {
        @include arrow('bottom','center', 10px, #66d069);
      }
    }

    &.card--primary {
      background: $clr-pri;
      //background: linear-gradient(45deg, rgba(5, 170, 255, 1) 0%, rgba(0, 128, 218, 1) 100%);
      .card__icon {
        line-height: 75px;
        .icon {
          font-size: 30px;
        }
      }
    }

    &.card--gray {
      background: #bdbdbd;
      //background: linear-gradient(45deg, rgba(224, 224, 224, 1) 0%, rgba(189, 189, 189, 1) 100%);
      &.active {
        @include arrow('bottom','center', 10px, #bdbdbd);
      }
      .card__icon {
        line-height: 70px;
        .icon {
          font-size: 26px;
        }
      }
    }

    &.card--neutral {
      background:$clr-amber;
      //background: linear-gradient(45deg, rgba(224, 224, 224, 1) 0%, rgba(189, 189, 189, 1) 100%);
      &.active {
        @include arrow('bottom','center', 10px, $clr-amber);
      }
      .card__icon {
        line-height:80px;
        .icon {
          font-size:35px;
        }
      }
    }

  }

}


