/*jQuery UI Datepicker 1.10.3*/

.ui-datepicker {
  background: $white;
  display: none;
  padding:10px;
  z-index:1;
  width: 100%;
  position: absolute;
  border: 1px solid #d6dade;
  top: 33px;
  @include border-radius(3px);

  .ui-datepicker-header {
    position: relative;
    margin: 0;
  }
  .ui-datepicker-prev,
  .ui-datepicker-next {
    position: absolute;
    top:0;
    width: 25px;
    height: 25px;
    text-align: center;
    cursor: pointer;
    &:before {
      text-indent: 0;
      font-size: 16px;
      position: relative;
      top:4px;
    }

    &:hover {
      background: #eeeeee;
    }
  }

  .ui-datepicker-prev {

    &:before {
      font-family: "Font Awesome 5 Pro";
      content: "\f104";
      display: inline-block;
    }
    left: 0;

  }
  .ui-datepicker-next {
    &:before {
      font-family: "Font Awesome 5 Pro";
      content: "\f105";
      display: inline-block;
    }
    right: 2px;

  }

  .ui-datepicker-prev span,
  .ui-datepicker-next span {
    display: none;
    position: absolute;
    left: 50%;
    margin-left: -8px;
    top: 50%;
    margin-top: -8px;
  }
  .ui-datepicker-title {
    margin: 0 2.3em;
    line-height: 1.8em;
    font-size:13px;
    text-align: center;
    select {
      margin-right: 1px;
      padding:2px;
    }
  }
  select {
    &.ui-datepicker-month-year {
      width: 100%;
    }
    &.ui-datepicker-month,
    &.ui-datepicker-year {
      width: 49%;
    }
  }
  table {
    width: 100%;
    font-size: .9em;
    border-collapse: collapse;
    margin: 0 0 .4em;
  }
  th {
    padding: .7em .3em;
    text-align: center;
    font-weight: 600;
    border: 0;
    font-size:13px;
  }
  td {
    border: 0;
    text-align: center;
    padding: 1px;
    font-size:13px;
    span,
    a {
      display: block;
      color: $black;
      padding: .5em .4em;
      //text-align: right;
      text-decoration: none;
      &:hover {
        color: $clr-pri;
      }
      &.ui-state-active {
        background: $clr-pri;
        color: $white;
      }
    }
    &.ui-datepicker-today {
      a {
        color: $black;
        background: $bg-gray;
        &.ui-state-active {
          background: $clr-pri;
          color: $white;
        }
      }
    }
    &.ui-state-disabled {
      opacity: .35;
      a {
        background: none;
        color: $black;
      }
    }
  }
  .ui-datepicker-buttonpane {
    background-image: none;
    margin: .7em 0 0 0;
    padding: 0 .2em;
    border-left: 0;
    border-right: 0;
    border-bottom: 0;
    button {
      float: right;
      margin: .5em .2em .4em;
      cursor: pointer;
      padding: .2em .6em .3em .6em;
      width: auto;
      overflow: visible;
      &.ui-datepicker-current {
        float: left;
      }
    }
  }
  &.ui-datepicker-multi {
    width: auto;
  }
}

/* with multiple calendars */

.ui-datepicker-multi .ui-datepicker-group {
  float: left;
  table {
    width: 95%;
    margin: 0 auto .4em;
  }
}

.ui-datepicker-multi-2 .ui-datepicker-group {
  width: 50%;
}

.ui-datepicker-multi-3 .ui-datepicker-group {
  width: 33.3%;
}

.ui-datepicker-multi-4 .ui-datepicker-group {
  width: 25%;
}

.ui-datepicker-multi {
  .ui-datepicker-group-last .ui-datepicker-header,
  .ui-datepicker-group-middle .ui-datepicker-header {
    border-left-width: 0;
  }
  .ui-datepicker-buttonpane {
    clear: left;
  }
}

.ui-datepicker-row-break {
  clear: both;
  width: 100%;
  font-size: 0;
}

/* RTL support */

.ui-datepicker-rtl {
  direction: rtl;
  .ui-datepicker-prev {
    right: 2px;
    left: auto;
  }
  .ui-datepicker-next {
    left: 2px;
    right: auto;
  }
  .ui-datepicker-prev:hover {
    right: 1px;
    left: auto;
  }
  .ui-datepicker-next:hover {
    left: 1px;
    right: auto;
  }
  .ui-datepicker-buttonpane {
    clear: right;
    button {
      float: left;
      &.ui-datepicker-current {
        float: right;
      }
    }
  }
  .ui-datepicker-group {
    float: right;
  }
  .ui-datepicker-group-last .ui-datepicker-header,
  .ui-datepicker-group-middle .ui-datepicker-header {
    border-right-width: 0;
    border-left-width: 1px;
  }
}
