*,
*:before,
*:after {
  box-sizing: border-box; }

::-moz-selection {
  color: #fff;
  background: #59b9ef;
  text-shadow: none; }

::selection {
  color: #fff;
  background: #59b9ef;
  text-shadow: none; }

:-moz-placeholder {
  /* Firefox 18- */ }

::-moz-placeholder {
  /* Firefox 19+ */ }

html {
  -ms-text-size-adjust: 100%;
  -webkit-text-size-adjust: 100%;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  -webkit-tap-highlight-color: transparent;
  font-size: 62.5%; }

body {
  margin: 0;
  font-family: Helvetica, Arial, sans-serif;
  cursor: default;
  text-shadow: none; }

iframe {
  border: 0; }

h1,
h2,
h3,
h4,
h5,
p,
ul,
ol,
blockquote,
figure,
button {
  margin: 0;
  padding: 0; }

main,
li {
  display: block; }

h1,
h2,
h3,
h4,
h5,
p {
  font-size: inherit; }

strong {
  font-weight: bold; }

sup {
  position: relative;
  top: -.5em;
  font-size: 65%;
  line-height: 0;
  vertical-align: baseline; }

textarea:focus,
input:focus {
  outline: none; }

a,
button {
  color: inherit; }

a {
  text-decoration: none; }

button {
  overflow: visible;
  border: 0;
  font: inherit;
  letter-spacing: inherit;
  background: none;
  cursor: pointer;
  -webkit-font-smoothing: inherit; }
  button:focus {
    outline: 0; }

::-moz-focus-inner {
  padding: 0;
  border: 0; }

img {
  max-width: 100%;
  height: auto;
  border: 0; }

input[type=search] {
  box-shadow: none; }

[role="button"] {
  cursor: pointer; }

/*  input {
  -webkit-appearance: none; // force iOS to not show rounded borders
}
    */
table {
  border-collapse: collapse;
  border-spacing: 0; }

