$header-border: #d6dbdf;
$user-color: #4c5e70;
.header {
  width: 100%;
  height:70px;
  background-color: $white;
  @include transition();
  position: fixed;
  z-index: map-get($zindex, header);

/*  .fixed &{
   // box-shadow: none;
    border-bottom: 1px solid #ebebeb;
    height: 65px;
  }*/

//  z-index: 50;
  top: 0;
  left: 0;
  box-shadow: 0px 3px 10px 0px rgba(0, 0, 0, 0.08);
  @include susy-breakpoint(1024px, $susy, '.no-mq') {
    height: 55px;
  }

  &.editable--widget {
    position: fixed;
    border-left: 0;
    border-right: 0;
    .editable--btn {
      bottom: -12px;
      top: auto;
      right: 10px;
    }
  }
}

.logo {
  float: left;
  height: 100%;
  text-align: center;
  padding: 0 20px;
  @include susy-breakpoint(1024px, $susy, ".no-mq") {
    padding:0;
    margin-left: 45px;
  }
  @include susy-breakpoint(480px, $susy, ".no-mq") {
    margin-left:40px;
  }

  a{
    outline: none;
  }
  img {
    vertical-align: middle;
    max-height: 60px;
    width: auto;
    border: 0;
    outline: none;
    margin-top: -5px;
    @include susy-breakpoint(480px, $susy, ".no-mq") {
      max-width: 140px;
    }
  }
  &:before {
    content: "";
    display: inline-block;
    height: 100%;
    vertical-align: middle;
  }
}

.header-options {
  float: right;
  padding:17px 20px 0 0;
 /* .fixed &{
    padding:15px 25px 0 0;
  }*/
  @include susy-breakpoint(1024px, $susy, ".no-mq") {
    padding-right: 15px;
    padding-top: 12px;
  }
  @include susy-breakpoint(480px, $susy, ".no-mq") {
    padding-right: 5px;
  }
  .user-info {
    float: right;
    font-size: 12px;
    font-weight: 400;
    cursor: pointer;
    position: relative;
    color: #808080;
    /*span {
      float: left;
      line-height: 18px;
      padding: 9px 13px 9px 0;
    }*/
    img {
      float: left;
      border-radius: 50%;
      max-width: 36px;
      border: 1px solid #d2d2d2;
    }
    i {
      font-size: 30px;
      margin-top: 1px;
      color: #d5d7dd;
      @include susy-breakpoint(480px, $susy, ".no-mq") {
        font-size: 24px;
        line-height: 30px;
      }
    }
    .user-dropdown-link {
      float: left;
      position: relative;
      display: block;
      padding: 0 25px 0 0;
      color: #808080;

      &:after {
        content: "\f107";
        font-size: 20px;
        font-family: "Font Awesome 5 Pro";
        font-style: normal;
        font-weight: 300;
        position: absolute;
        right: 6px;
        @include opacity(0.5);
        top: 50%;
        margin-top: -10px;
      }
      &:hover {
        color: #222222;
        i {
          border-color: #222222;
        }
      }
    }

  }
  .user-features {
    float: right;
    padding: 0 15px;
    @include susy-breakpoint(1024px, $susy, ".no-mq") {
      padding: 0 5px;
    }

    .feature-node {
      float: left;
      margin: 0 10px;
      position: relative;
      min-width: 36px;
      text-align: center;
      @include susy-breakpoint(1024px, $susy, ".no-mq") {
        margin: 0 5px;
      }
      @include susy-breakpoint(480px, $susy, ".no-mq") {
        margin: 0;
        min-width: auto;
      }
    }
    .notification-node {
      @include susy-breakpoint(1024px, $susy, ".no-mq") {
        display: none;
      }
    }
    .menu-node {
      display: none;
      @include susy-breakpoint(1024px, $susy, ".no-mq") {
        display: block;
      }
    }
    .handler {
      display: block;
      padding: 9px;
      color: #808080;
      border-radius: 50%;
      position: relative;
      @include susy-breakpoint(480px, $susy, ".no-mq") {
        padding: 7px;
      }
      i {
        font-size: 18px;
      }

      &:hover,&.active {
        background: #f1f6fa;
      }
    }
    .more-node {
      .fa-ellipsis-v {
        font-size: 28px;
      }
      .handler {
        padding: 4px;
      }
    }
    .has-notification {
    }
    a {

    }
  }

}

.circle {
  position: absolute;
  height: 7px;
  width: 7px;
  border-radius: 50%;
  background-color: #4da1ff;
  right: 9px;
  top: 4px;
}

