  top:0; right:0; bottom:0; left:0;
  border-radius:50%;
  //border:1px solid #4da1ff;
  box-shadow: 0px 0px 0px 1px #4da1ff;
}*/

.circle:before {
  -webkit-animation: ripple 2s linear infinite;
  animation: ripple 2s linear infinite;
}

.circle:after {
  -webkit-animation: ripple 2s linear 1s infinite;
  animation: ripple 2s linear 1s infinite;
}

@keyframes googleClick {
  to {
    transform: scale(2);
    opacity: 0;
  }
}

.wave.dark {
  background: rgba(33, 33, 33, 0.5);
}

.wave.light {
  background: rgba(255, 255, 255, 0.8);
}

.wave {
  position: absolute;
  left: 0;
  top: 0;
  width: 5px;
  height: 5px;
  background: rgba(255, 255, 255, 0.8);
  animation: googleClick 350ms linear forwards;
  opacity: 0.75;
  transform: scale(0);
  border-radius: 50%;
}

//@-webkit-keyframes ripple{
//  0% {-webkit-transform:scale(1); }
//  75% {-webkit-transform:scale(3.5); opacity:1;}
//  100% {-webkit-transform:scale(4); opacity:0;}
//}

@keyframes ripple {
  0% {
    transform: scale(1);
  }
  75% {
    transform: scale(3.5);
    opacity: 1;
  }
  100% {
    transform: scale(4);
    opacity: 0;
  }
}

//notification
.notification {

  position: relative;
  vertical-align: middle;

  @include susy-breakpoint(786px, $susy, '.no-mq') {
    color: #97a0a9;
    display: none;
  }

  .icon {
    &:before {
      font-size: emify(25, 12);

      @include susy-breakpoint(786px, $susy, '.no-mq') {
        // font-size: emify(25, 12);
      }
    }
  }

  .badge {
    // display: none;
    position: absolute;
    right: -12px;
    top: -2px;
