    color: $white;
    padding: 2px 4px;
    min-width: 17px;
    font-weight: 600;
    @include border-radius(20px);
    background: #e35092;
    //width: 17px;
    text-align: center;

    //height: 16px;
    //padding: emify(3, 12);
  }
}

.portal-translation {

  position: relative;
  vertical-align: middle;

  @include susy-breakpoint(786px, $susy, '.no-mq') {
    color: #97a0a9;
  }


}

.user {
  border-bottom: 1px solid #ebebeb;
  position: relative;
  padding: 10px 10px 10px 52px;
  background: #f9f9f9;

  p {
    line-height: 15px;
    strong {
      display: block;
      font-weight: 500;
    }
  }

  .user__inital {
    border: 1px solid $clr-pri;
    border-radius: 100%;
    font-size: 18px;
    width: 35px;
    position: absolute;
    text-transform: uppercase;
    left: 10px;
    color: $clr-pri;
    background: $white;
    top: 7px;
    text-align: center;
    line-height: 35px;
    height: 35px;
  }

}

.user-dropdown {
  border: 1px solid #c8c8c8;
  box-shadow: 0 0 6px 0 rgba(0, 0, 0, .16);
  position: absolute;
  right: 0;
  top: 40px;
  background: $white;
  width: 183px;
  border-radius: 3px;
  opacity: 0;
  visibility: hidden;
  transform: translateY(20px);
  @include transition();
  &.active {
    opacity: 1;
    visibility: visible;
    transform: translateY(0);
  }
  .user__list {
    ul {
      li {
        display: block;

        a {
          display: block;
          position: relative;
          margin: 0;
          font-size: 12px;
          padding: 7px 10px 7px 30px;
          color: #797979;


          i {
            position: absolute;
            text-align: center;
            margin: 0;
            top: 6px;
            line-height: normal;
            left: 10px;
            color: #333;
            font-size: 14px;
            font-weight: 300;
            vertical-align: middle;
          }
          &:hover {
            color: #666;
            background-color: #eaeaea;
          }
        }
      }
    }
  }
}

.nav-toggle {
  //display: inline-block;
  display: none;
  color: #97a0a9;

  position: relative;
  top:2px;
  margin: 0 5px;
  vertical-align: middle;
  .icon {
    &:before {
      font-size: 32px;
      vertical-align: middle;
    }
  }

  &.active {
    color: $clr-pri;
  }

  @include susy-breakpoint(1024px, $susy, '.no-mq') {
    display: inline-block;
  }
}




.notification-node .noti-dot {
  position: relative;
  top: 5px;
}

.notification-node .pulse {
  width: 2px;
  height: 2px;
  border: 3px solid $clr-pri;
  border-radius: 30px;
  background-color: $clr-pri;
  z-index: 10;
  position: absolute;
  right: 8px;
}

.notification-node .dot {
  border: 4px solid $clr-pri;
  background: transparent;
  border-radius: 60px;
  height: 30px;
  width: 30px;
  -webkit-animation: pulse 3s ease-out;
  animation: pulse 3s ease-out;
  -webkit-animation-iteration-count: infinite;
  animation-iteration-count: infinite;
  position: absolute;
  top: -12px;
  right: -4px;
  z-index: 1;
  opacity: 0;
}

@-webkit-keyframes pulse {
  0% {
    -webkit-transform: scale(0);
    opacity: 0.0;
  }
  25% {
    -webkit-transform: scale(0);
    opacity: 0.1;
  }
  50% {
    -webkit-transform: scale(0.1);
    opacity: 0.3;
  }
  75% {
    -webkit-transform: scale(0.5);
    opacity: 0.5;
  }
  100% {
    -webkit-transform: scale(1);
    opacity: 0.0;
  }
}

