//loader overlay
.loader-overlay {
  background-color: $white;
  height: 100%;
  width: 100%;
  position: fixed;
  @include opacity(0.9);
  top: 0;
  bottom: 0;
  left: 0;
  z-index: map-get($zindex, overlay);
}

.loader-container {
  background-color: $widget-bg;
  position: relative;
  width: 100%;
  padding: 10px;
  border-radius: 3px;
  height:250px;
  box-shadow: 0 0 6px 0 rgba(0, 0, 0, .2);

  &.widget--filters{
    height: 100px;
  }

  &.widget--heightauto{
    height:50px;
  }
}

.loader {
  position: absolute;
  left: 50%;
  top: 50%;
  width: 40px;
  height: 16px;
  margin: -8px 0 0 -20px;
}

