//social media
.page-socialmedia {

}

.response {
  font-weight: bold;
  font-size: 11px;
  .icon {
    font-size: 20px;
  }
  span {
    display: block;

  }

  &.response--positive {
    color: $clr-green;
  }

  &.response--negative {
    color: $clr-red;
  }

  &.response--neutral {
    color: $clr-amber;
  }
}

.network {
  position: relative;
  padding-left: 35px;
  font-size: 15px;
  color: #3d5c98;

  .icon {
    position: absolute;
    width: 25px;
    border-radius: 5px;
    text-align: center;
    line-height: 25px;
    height: 25px;
    left: 0;
    top: -3px;
    background: #3d5c98;
    color: $white;

    &:before {
      font-style: normal;
      font-family: icomoon;;
      content: "\f09a";
      font-size: 16px;

    }

  }

  &.twitter {
    color: #2ec4f6;
    .icon {
      background: #2ec4f6;
      color: $white;
      &:before {
        content: "\f099";
      }
    }

  }

}

.initial {
  width: 20px;
  height: 20px;
  display: inline-block;
  border-radius: 100%;
  background: $clr-amber;
  text-align: center;
  color: $white;
  line-height: 20px;
  text-transform: uppercase;
  font-size: 10px;

  &.initial--big {
    width: 38px;
    height: 38px;
    font-size: 17px;
    line-height: 38px;
  }

  &.green {
    background: $clr-green;
  }
}

.icons-list {
  .icon {
    position: relative;
    top: 3px;
    font-size: 18px;
    display: inline-block;
    margin: 0 5px;
  }
}

.table--social {

  table {
    tr {
      background: $white;
      border: 0;
      //box-shadow: 0 0 6.6px 4.4px hsla(0, 0%, 97%, .79);

      td {
        text-align: left;
        padding: 10px;
        border-bottom: 1px solid #cfcfcf;

        &.comments{
          min-width:160px;
        }

        &.minwidth{
          min-width:100px;
        }


        &.reply {
          td {
            vertical-align: top;
          }
        }

        &.seed {
          font-weight: 600;
          color: $black;

          span {

            i {
              color: $clr-pri;
            }
          }
        }

        &.keyword {
          span {
            display: inline-block;
            padding: 5px 10px;
            background: #f5f5f5;
            i {
              color: $clr-pri;
            }
          }

        }
      }

      &.blank {
        background: transparent;
        td {
          padding: 5px 10px;
          border: 0;
        }
      }

      &.child {
        td {
          border: 0;
        }

        &.indent--one {

          .icons-list {
            padding-right: 30px;

          }
        }

        &.indent--one {

          .icons-list {
            padding-left: 30px;
          }
        }

      }
    }

  }

}

.footer {
  margin: 10px 0;
  float: left;
  width: 100%;
  text-align: center;
}

.sm-pagination {
  li {
    display: inline-block;
    font-size: 11px;
    margin: 0 10px;
  }
}

.filter-sm {
  float: right;
  .filter__item {
    float: left;
    margin-left: 10px;
    .sm-pagination {
      margin-top: 8px;
    }
  }
}

.filter-menu {
  position: relative;
  .menu__btn {
    background: $clr-pri;
    border-radius: 2px;
    color: $white;
    padding: 7px 10px;
    display: inline-block;
    font-size: 12px;
    box-shadow: 0px 0px 6.6px 4.4px rgba(247, 247, 247, 0.004);

    i {
      font-size: 12px;
      margin-left: 10px;
    }
    &:hover {
      color: $white;
    }
  }
  .menu__dropdown {
    position: absolute;
    right: -5px;
    top: 100%;
    width: 140px;
    padding: 10px 13px;
    margin: 9px 0 0 0;
    font-size: 12px;
    color: #34495e;
    background: $white;
    border: 1px solid $clr-pri;
    border-radius: 5px;
    box-shadow: 0px 0px 6.48px 1.52px rgba(146, 146, 146, 0.32);

    transform: translateY(45px);
    @include transition();
    visibility: hidden;
    @include opacity(0);
    z-index: 2;
    &:before {
      content: '';
      position: absolute;
      right: 12px;
      top: -5px;
      width: 9px;
      height: 9px;
      background: $white;
      border: 1px solid $clr-pri;
      border-width: 1px 0 0 1px;
      transform: rotate(45deg);
    }
    ul {
      li {
        border-top: 1px solid #e5e5e5;
        padding: 2px;
        &:first-child {
          border-top: none;
          a {
            color: #34495e;
            &:hover {
              color: $clr-pri;
            }
          }
        }
        label {
          float: left;
          line-height: 16px;;
        }
        &.admin-user-switch {
          padding-top: 4px;
          padding-bottom: 4px;
          @include clearfix();
          .toggle {
            float: right;
            padding: 0 2px 0 0;
          }
        }
      }
    }
    &.active {
      transform: translateY(0);
      @include opacity(1);
      visibility: visible;
    }
  }
}

.dropdown-list {
  display: inline-block;
  font-size: 0;

  > li {
    display: inline-block;
    position: relative;
    padding: 3px 2px 2px;
    border: 1px solid #cbcbcb;

    &:last-child {
      border-left: 0;
      margin-left: -3px;
    }
  }
}

.reply-form {
  //margin-top: 10px;
  width: 80%;
  .field {
    float: left;
    padding-top: 3px;

    &.submit-btn {
      a {
        display: inline-block;
        margin: 15px 0 0 15px;
        color: $clr-pri;
        .icon {
          font-size: 25px;

        }

        &:hover {
          color: gray;
        }
      }
    }
    .field-validation-valid {
      display: block;
      clear: both;
      color: $clr-red;
    }
  }
  textarea {
    height: 52px;
    width: 450px;
  }
}

.feedback {
  max-width: 300px;
  .feedback__content {
    // max-width:300px;
  }
}

.btn--social {
  .social-count {
    padding: 2px 10px;
    border-radius: 20px;
    background: #e7e4e4;
    font-size: 12px;
    color: #666;
    display: inline-block;
    margin-left: 5px;

  }
}